  pointer-events: none;
  z-index: 101;
  inset: var(--block-position) var(--inline-position) var(--block-position) var(--inline-position);
  overscroll-behavior: none;
  opacity: 0;
  background-color: #191536;
  width: auto;
  height: auto;
  position: fixed;
  overflow: hidden auto;
  transform: translateX(-100%);
}

